import React, { Component } from 'react';
import Form from 'react-bootstrap/Form';
import InputGroup from 'react-bootstrap/InputGroup';
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import { toast } from 'react-toastify';
import {
  get_categorys_or_pages,
  get_posts_sheet
} from './lib/axios'
export default class Google_shoping_sheet extends Component {
  constructor (props) {
    super(props)
    this.state = {
      posts:[
        // {
        //   id:'0',
        //   url:"#",
        //   img:'https://anbinhnew.com/wp-content/uploads/2021/01/Giuong-sat-don-Hoang-Gia-mau-HG02-300x300.jpg',
        //   title:'Ging st gi r -hp vung gim gi u i khuyn mi cao',
        //   status:'publish',
        //   price_og:'2700000',
        //   price_sale:'2600000',
        //   short_des:'y l mu ging st ang c a chung nht th trng hin ti bi v gi thnh v mu m u  top nhng sn phm ging bn chy nht Vit Nam. Ging c lm bng st hp cao cp, kt hp cch x l bng sn tnh in nn mu sc p v sinh ng. m bo bn s rt thch th khi chim ngng trc tip.',
        //   id_parent:'',
        // },
        // {
        //   url:"#",
        //   img:'https://anbinhnew.com/wp-content/uploads/2021/01/Giuong-sat-don-Hoang-Gia-mau-HG02-300x300.jpg',
        //   title:'Ging st gi r -hp vung gim gi u i khuyn mi cao',
        //   status:'publish',
        //   id:'1',
        //   price_og:'2700000',
        //   price_sale:'2600000',
        //   short_des:'y l mu ging st ang c a chung nht th trng hin ti bi v gi thnh v mu m u  top nhng sn phm ging bn chy nht Vit Nam. Ging c lm bng st hp cao cp, kt hp cch x l bng sn tnh in nn mu sc p v sinh ng. m bo bn s rt thch th khi chim ngng trc tip.',
        //   id_parent:'',
        //   id_parent:1,
        // },
      ],
      cate:[],
      selected_cate:{
        id:'-1',
        name:'Cha chn danh mc no',
        img:''
      },
      gtin:'',
      mpn:'',
      nhan_hieu:'',
      danh_muc_sp_google:'',
      loai_sp:'',
      is_price_og:false
    }
  }
  async componentDidMount(){
    let a= await get_categorys_or_pages(-1);
    this.setState({cate:a})
  }
  render() {
      let {cate,selected_cate,posts,gtin,mpn,nhan_hieu,danh_muc_sp_google,loai_sp,is_price_og}=this.state;
      
    return (
      <React.Fragment>
        <div>
          <div className='container controlz'>
            <div className='row controlk' style={{marginTop:'5px'}}>
              <div className='col-8'>
                <h3>Chn Danh mc</h3>
                <div className='gg1'>
                  <DropdownButton
                    as={ButtonGroup}
                    key={0}
                    variant={'Secondary'}
                    title={selected_cate.name}
                  >
                    <Dropdown.Item eventKey={-1}
                          onClick={()=>{
                            this.setState({
                              selected_cate:{
                                id:'-1',
                                name:'Cha chn danh mc no',
                                img:''
                              },
                              posts:[]
                            })
                          }}
                        >
                        <img src="" className="imgxd"/>
                        <span>Khng chn danh mc</span>
                        </Dropdown.Item>
                    {
                    cate.map((e,i)=>{
                      return (
                        <Dropdown.Item eventKey={e.id} key={e.id}
                          onClick={async ()=>{
                            // xu ly get data here
                            let a= await get_posts_sheet(e.id);
                            console.log(" ~ file: Google_shoping_sheet.js:95 ~ Google_shoping_sheet ~ onClick={ ~ a", a)
                            this.setState({
                              posts:a,
                              selected_cate:{
                                id:e.id,
                                name:e.title,
                                img:e.img
                              },
                            })
                          }}
                        >
                        <img src={e.img} className="imgxd"/>
                        <span>{e.title}</span>
                        </Dropdown.Item>
                      )})
                    }
                  </DropdownButton>
                </div>
                <div className='gg2'>
                  {selected_cate.img&&<img src={selected_cate.img} className="imgxd"/>}
                </div>
                  
              </div>
              <div className='col-4'>
                <button className='buzz' onClick={()=>{this.copyTable()}}>Copy bng <i class="fa-regular fa-copy" style={{color:"blue"}}></i></button>
              </div>
            </div>
            <div className='row'>
              <Form.Label htmlFor="basic-url">Thng tin iu chnh:</Form.Label>
              <div className='wrap-input col-4'>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      gtin:
                    </InputGroup.Text>
                    <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                      value={gtin}
                      onChange={(e)=>{
                        let {gtin}=this.state;
                        gtin=e.target.value
                        this.setState({gtin:gtin})
                      }}
                    />
                  </InputGroup>
              </div>
              <div className='wrap-input col-4'>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      mpn:
                    </InputGroup.Text>
                    <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                      value={mpn}
                      onChange={(e)=>{
                        let {mpn}=this.state;
                        mpn=e.target.value
                        this.setState({mpn:mpn})
                      }}
                    />
                  </InputGroup>
              </div>
              <div className='wrap-input col-4'>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      Nhn hiu:
                    </InputGroup.Text>
                    <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                      value={nhan_hieu}
                      onChange={(e)=>{
                        let {nhan_hieu}=this.state;
                        nhan_hieu=e.target.value
                        this.setState({nhan_hieu:nhan_hieu})
                      }}
                    />
                  </InputGroup>
              </div>
              <div className='wrap-input col-8'>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      Danh mc sn phm ca google:
                    </InputGroup.Text>
                    <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                      value={danh_muc_sp_google}
                      onChange={(e)=>{
                        let {common}=this.state;
                        danh_muc_sp_google=e.target.value
                        this.setState({danh_muc_sp_google:danh_muc_sp_google})
                      }}
                    />
                  </InputGroup>
              </div>
              <div className='wrap-input col-4'>
                  <InputGroup className="mb-3">
                    <InputGroup.Text id="basic-addon3">
                      Loi sn phm:
                    </InputGroup.Text>
                    <Form.Control placeholder='...' aria-describedby="basic-addon3" 
                      value={loai_sp}
                      onChange={(e)=>{
                        let {loai_sp}=this.state;
                        loai_sp=e.target.value
                        this.setState({loai_sp:loai_sp})
                      }}
                    />
                  </InputGroup>
              </div>
              <div className='wrap-input col-4'>
                <Form className='foncms'>
                  <Form.Check 
                    type='checkbox'
                    label={`Gi gc`}
                    checked={is_price_og}
                    onChange={()=>{
                      this.setState({
                        is_price_og:true
                      })
                    }}
                  />

                  <Form.Check
                    type='checkbox'
                    label={`Gi sale`}
                    style={{marginLeft:"15px"}}
                    checked={!is_price_og}
                    onChange={()=>{
                      this.setState({
                        is_price_og:false
                      })
                    }}
                  />
                </Form>
              </div>
              <table className='tabl-body'>
                {
                  posts.map((e,i)=>{
                    return(<tr key={i}>
                      <td>ABz{e.id}</td>
                      <td>{e.title}</td>
                      <td>{e.short_des}</td>
                      <td>{e.url}</td>
                      <td>Mi</td>
                      <td>{is_price_og?e.price_og:e.price_sale} VND</td>
                      <td>in stock</td>
                      <td>{e.img}</td>
                      <td>{gtin}</td>
                      <td>{mpn}</td>
                      <td>{nhan_hieu}</td>
                      <td>{danh_muc_sp_google}</td>
                      <td>{loai_sp}</td>
                    </tr>)
                  })
                }

              </table>  
        
            </div>
          </div>
 
        </div>
     
      </React.Fragment>
    );
  }
  copyTable = () => {
    const elTable = document.querySelector('table');
    
    let range, sel;
    
    // Ensure that range and selection are supported by the browsers
    if (document.createRange && window.getSelection) {
    
      range = document.createRange();
      sel = window.getSelection();
      // unselect any element in the page
      sel.removeAllRanges();
    
      try {
        range.selectNodeContents(elTable);
        sel.addRange(range);
      } catch (e) {
        range.selectNode(elTable);
        sel.addRange(range);
      }
    
      document.execCommand('copy');
      toast.success(' copy',{theme: "colored"})
    }
    
    sel.removeAllRanges();
    
    
    }
}

